import React from "react";
import { connect } from "react-redux";
import * as ScheduleActions from "../../store/Schedule/scheduleAction";
import * as JobDataAction from "../../store/JobData/JobDataAction";
import * as JobDescriptionAction from "../../store/JobDescription/JobDescriptionAction";
import * as TestAction from "../../store/Assessment/AssessmentAction";
import * as CActions from "../../store/Resumesource/resumesourceAction";
import { bindActionCreators } from "redux";
import { Card, CardContent } from "@material-ui/core";
import Button from "../../components/Controls/Button/Button";
import { withRouter } from "react-router-dom";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import { ReactComponent as FileCopyIcon } from "../../assets/images/schedule/copy.svg";
import DatePicker from "../../components/DatePicker/Datepicker";
import TimePicker from "../../components/DatePicker/TimePicker";
import ClearIcon1 from "../../assets/images/schedule/close.svg";
import Chevron from "../../assets/images/schedule/right-chevron.svg";
import "./Schedule.css";
import TableComponent from "../../components/Controls/Table/sortableTable";
import PrintModal from "../../components/printModal";
import { Link } from "react-router-dom";
import Checkbox from "@material-ui/core/Checkbox";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";
import Successmodal from "../../components/Successmodal/Succesmodal";
import DeleteIcon from "@material-ui/icons/Delete";
import { store } from "react-notifications-component";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import { secondsToMinutes } from "../../utils/utility";
import RichTextEditor, {
  getTextAlignClassName,
  getTextAlignStyles,
  getTextAlignBlockMetadata,
} from "react-rte";
import TimezoneSelect from "react-timezone-select";
import AttachFileIcon from "@material-ui/icons/AttachFile";
import ReactFileReader from "react-file-reader";
import moment from "moment";
import JoditEditor from "jodit-react";
import Question from "../../assets/images/Question.svg";
import Candidates from "../../assets/images/Candidates.svg";
import Pagination from "../../components/Atoms/Pagination";
import CandidatesNew from "../../assets/images/CandidatesNew.svg";
import SpeakingIcon from "../../assets/images/speaking.svg";
import McqIcon from "../../assets/images/mcq.svg";
import Coding from "../../assets/images/Assessment/Coding.svg";
import ReactGA from "react-ga";
const dropdonVal = JSON.parse(localStorage.getItem("dropdown"));
const config = {
  readonly: false,
  height: 300,
  width: 587,
};
class Schedule extends React.Component {
  constructor(props) {
    super(props);
    this.editor = React.createRef(null);
    this.state = {
      content: "",
      scheduleid: "",
      jobtitle: [],
      id: dropdonVal && dropdonVal.value,
      name: dropdonVal && dropdonVal.label,
      job: "",
      optid: dropdonVal && dropdonVal.value,
      optname: "",
      des: RichTextEditor.createEmptyValue(),
      search: "",
      startDate: moment(),
      endDate: moment().add(7, "d"),
      link: "https://www.cronj.com/birbal/",
      copySuccess: false,
      timefrom: moment(new Date())
        .hours(0)
        .minutes(0)
        .seconds(0)
        .milliseconds(0),
      timeTo: moment(new Date()).hours(0).minutes(0).seconds(0).milliseconds(0),
      selectCandidates: false,
      cbg: false,
      openRes: "",
      jobTitleList: [],
      job_id: "",
      candidates: "0",
      questionscount: "0",
      timerDisplay: "0 Min",
      candidateid: "",
      questionid: "",
      isShowImportModal: false,
      page: 1,
      page2: 1,
      limit: 10,
      limit2: 10,
      cdata: [],
      quesData: [],
      isShowQuesModal: false,
      question_data: [],
      candidate_details: [],
      open: false,
      generatelink: "",
      questiontime: "",
      jobTitleErr: "",
      testErr: "",
      messageErr: false,
      generateLinkErr: false,
      testOptions: [],
      allowedDevicesOptions: [
        { value: "LAPTOP", label: "Only PC" },
        { value: "MOBILE", label: "Only Mobile" },
        { value: "ALL_DEVICE", label: "Both PC And Mobile" },
      ],
      testSelected: null,
      timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      allowedDevices: "ALL_DEVICE",
      filterCan: "ALL",
      totalPage: 1,
      totalPage2: 1,
      showModal: false,
      currentQues: "",
      timeZoneErr: "",
      quesDataArrived: true,
      candidateData: [],
      currentCandidate: "",
      showModalNew: false,
      optionsdata: [],
      dropdownData: [],
      sendSchedule: false,
      scheduleCount: 0,
      totalScore: 0,
      totalVerbalScore: 0,
      totalMcqScore: 0,
      totalCodingScore: 0,
      totalVerbalDuration: 0,
      totalMcqDuration: 0,
      totalCodingDuration: 0,
      totalverbal: 0,
      totalMcq: 0,
      totalCoding: 0,
    };
  }
  async componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    this.props.JobDescriptionAction.getJobDescriptionData();
    const payload = {
      jdId: dropdonVal && dropdonVal.value,
      page: 1,
      limit: this.state.limit,
    };
    if (
      this.props &&
      this.props.location &&
      this.props.location.state &&
      this.props.location.state.jdId &&
      this.props.location.state.jdName &&
      this.props.location.state.fromJD &&
      this.props.location.state.filterCan
    ) {
      let value = this.props.location.state.jdId;
      let label = this.props.location.state.jdName;
      let filterCan = this.props.location.state.filterCan;

      this.setState({ id: value, name: label, filterCan: filterCan });

      const payload1 = {
        value: value,
        label: label,
      };
      localStorage.setItem("dropdown", JSON.stringify(payload1));
      const payload = {
        jdId: value,
        page: 1,
        limit: this.state.limit,
      };

      this.getdata(payload);
      this.getquestion({
        jdId: value,
        type: "dropdown",
      });
      let mergelink = this.state.link + value;
      this.setState({
        optid: value,
        optname: label,
        generatelink: mergelink,
      });
    } else if (
      this.props &&
      this.props.location &&
      this.props.location.state &&
      this.props.location.state.jdId &&
      this.props.location.state.jdName &&
      this.props.location.state.assessmentName &&
      this.props.location.state.assessmentId
    ) {
      let value = this.props.location.state.jdId;
      let label = this.props.location.state.jdName;
      let assessment = {
        value: this.props.location.state.assessmentId,
        label: this.props.location.state.assessmentName,
      };
      this.setState({
        id: value,
        name: label,
        testSelected: assessment,
      });

      const payload1 = {
        value: value,
        label: label,
      };
      localStorage.setItem("dropdown", JSON.stringify(payload1));
      const payload = {
        jdId: value,
        page: 1,
        limit: this.state.limit,
      };

      this.getdata(payload);
      this.getquestion({
        jdId: value,
        type: "dropdown",
      });
    } else {
      if (dropdonVal && dropdonVal.value) {
        this.getdata(payload);
        this.getquestion({
          jdId: dropdonVal && dropdonVal.value,
          type: "dropdown",
        });
      }
    }
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.optid !== this.state.optid && this.state.optid) {
      this.props.resumesourceAction.getCandidateById({
        jdId: this.state.optid,
        page: 1,
        limit: this.state.limit,
      });
      this.setState({ page: 1 });
    }

    if (
      (prevProps.resumesourceReducer &&
        prevProps.resumesourceReducer.getCandidateByIdData) !==
        (this.props.resumesourceReducer &&
          this.props.resumesourceReducer.getCandidateByIdData) &&
      this.props.resumesourceReducer.getCandidateByIdData
    ) {
      if (
        this.state.sendSchedule &&
        this.state.scheduleCount !== 0 &&
        this.props.resumesourceReducer.getCandidateByIdData &&
        this.props.resumesourceReducer.getCandidateByIdData.results &&
        this.props.resumesourceReducer.getCandidateByIdData.results.length ===
          this.state.scheduleCount
      ) {
        const data =
          this.props.resumesourceReducer.getCandidateByIdData.results;
        const candidatelist = [];
        data &&
          data.length > 0 &&
          data.map((el) => {
            candidatelist.push(el.id);
          });

        const payload = {
          jdId: this.state.optid,
          assessmentId:
            this.state.testSelected && this.state.testSelected.value,
          scheduleMessage: this.state.content,
          // interviewLink: this.state.generatelink,
          candidates: candidatelist,
          fromTimestamp:
            this.state.startDate && this.state.timefrom
              ? `${this.GetFormattedDate(
                  new Date(this.state.startDate)
                )} ${moment(new Date(this.state.timefrom)).format("HH:mm:ss")}`
              : null,
          toTimestamp:
            this.state.endDate && this.state.timeTo
              ? `${this.GetFormattedDate(
                  new Date(this.state.endDate)
                )} ${moment(new Date(this.state.timeTo)).format("HH:mm:ss")}`
              : null,

          modeOfTest: this.state.allowedDevices,
          timezone:
            this.state.timeZone && this.state.timeZone.value
              ? this.state.timeZone.value
              : this.state.timeZone,
        };
        this.props.ScheduleAction.createSchedule(payload);
      } else {
        this.setState({
          candidateData:
            this.props.resumesourceReducer.getCandidateByIdData &&
            this.props.resumesourceReducer.getCandidateByIdData.results,
          totalPage:
            this.props.resumesourceReducer.getCandidateByIdData &&
            this.props.resumesourceReducer.getCandidateByIdData.totalPages,
        });
      }
    }
    if (
      prevProps.ScheduleReducer !== this.props.ScheduleReducer &&
      this.props.ScheduleReducer &&
      this.props.ScheduleReducer.isScheduleCreated
    ) {
      this.props.ScheduleReducer.isScheduleCreated = false;

      const payload = {
        jdId: this.state.id,
        page: 1,
        limit: this.state.limit,
      };

      this.props.resumesourceAction.getCandidateById(payload);
      this.props.JobDescriptionAction.getJobDescriptionData();
      this.props.testAction.getAllTest({
        jdId: this.state.id,
        type: "dropdown",
      });
      this.setState({
        scheduleCount: 0,
        sendSchedule: false,
        id: "",
        name: "",
        content: "",
        timeZoneErr: "",
        quesData: [],
        candidateData: [],
        testSelected: null,
        testOptions: [],
      });
    }

    if (
      prevProps.testReducer.allTest !== this.props.testReducer.allTest &&
      this.props.testReducer.allTest &&
      this.props.testReducer.allTest.data &&
      this.props.testReducer.allTest.data.length > 0
    ) {
      const testOptions = [];
      let data = this.props.testReducer.allTest.data;
      data.map((el) => {
        if (el.creationComplete) {
          testOptions.push({ value: el.id, label: el.name, data: el });
        }
      });
      this.setState({ testOptions: testOptions });
    }
    if (
      prevProps.JobDescriptionReducer !== this.props.JobDescriptionReducer &&
      this.props.JobDescriptionReducer.getJobDescriptionData &&
      this.props.JobDescriptionReducer.getJobDescriptionData.length > 0 &&
      this.props.JobDescriptionReducer.isGetJobDescriptionData
    ) {
      let dropdownData = this.props.JobDescriptionReducer.getJobDescriptionData;
      let optionsdata = dropdownData.map((d) => ({
        value: d.id,
        label:
          " " +
          (d.title && d.title) +
          " - " +
          d.minExp +
          " to " +
          d.maxExp +
          " Years of experience",
      }));
      this.setState({ dropdownData: dropdownData, optionsdata: optionsdata });
    }
    if (
      prevState.testSelected !== this.state.testSelected &&
      this.state.testSelected &&
      this.state.testSelected.data
    ) {
      const data = this.state.testSelected.data;
      this.props.testAction.getTestById({ id: data && data.id });
    }
    if (
      (prevProps.testReducer && prevProps.testReducer.testById) !==
        (this.props.testReducer && this.props.testReducer.testById) &&
      this.props.testReducer.testById &&
      this.props.testReducer.testById.data
    ) {
      const { verbalQuestions, mcqQuestions, codingQuestions } =
        this.props.testReducer.testById.data;
      let totalScore = 0,
        totalVerbalScore = 0,
        totalMcqScore = 0,
        totalCodingScore = 0,
        totalVerbalDuration = 0,
        totalMcqDuration = 0,
        totalCodingDuration = 0,
        totalverbal = verbalQuestions && verbalQuestions.length,
        totalMcq = mcqQuestions && mcqQuestions.length,
        totalCoding = codingQuestions && codingQuestions.length;
      verbalQuestions &&
        verbalQuestions.length > 0 &&
        verbalQuestions.map((el) => {
          totalScore += el.score;
          totalVerbalDuration += el.id && el.id.suggestedDuration;
          totalVerbalScore += el.score;
        });
      mcqQuestions &&
        mcqQuestions.length > 0 &&
        mcqQuestions.map((el) => {
          totalScore += el.score;
          totalMcqDuration += el.id && el.id.suggestedDuration;
          totalMcqScore += el.score;
        });
      codingQuestions &&
        codingQuestions.length > 0 &&
        codingQuestions.map((el) => {
          totalScore += el.score;
          totalCodingDuration += el.id && el.id.suggestedDuration;
          totalCodingScore += el.score;
        });
      this.setState({
        totalMcq,
        totalMcqDuration,
        totalMcqScore,
        totalScore,
        totalVerbalDuration,
        totalverbal,
        totalVerbalScore,
        totalCodingScore,
        totalCodingDuration,
        totalCoding,
      });
    }
  }

  handleUpdate = (event) => {
    const editorContent = event;
    var parser = new DOMParser();

    this.setState({
      content: editorContent,
    });
  };

  handleCloseSuccessModal = () => {
    this.setState({ open: false });
  };
  getdata(payload) {
    this.props.resumesourceAction.getCandidateById(payload);
  }
  async getquestion(payload) {
    this.props.testAction.getAllTest(payload);
  }

  handleChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };
  onChange = (value) => {
    this.setState({ des: value });
    if (this.props.onChange) {
      this.props.onChange(value.toString("html"));
    }
  };

  handleSelect = async (e) => {
    await this.setState({ id: e.value, name: e.label });
    const payload1 = {
      value: e.value,
      label: e.label,
    };
    localStorage.setItem("dropdown", JSON.stringify(payload1));
    const payload = {
      jdId: e.value,
      page: 1,
      limit: this.state.limit,
    };

    //  this.props.resumesourceAction.getCandidateById(payload);

    this.getdata(payload);
    this.getquestion({
      jdId: e.value,
      type: "dropdown",
    });
    let mergelink = this.state.link + e.value;
    this.setState({
      optid: e.value,
      optname: e.label,
      generatelink: mergelink,
    });
  };

  validateFields = () => {
    let validate = true;
    if (this.state.name === "") {
      validate = false;
      this.setState({ jobTitleErr: "Please select job title" });
    }
    if (this.state.content === "") {
      validate = false;
      this.setState({ messageErr: "Please enter message" });
    }
    if (this.state.testSelected === null || !this.state.testSelected.value) {
      validate = false;
      this.setState({ testErr: "select assessment" });
    }

    if (this.state.timeZone === "") {
      validate = false;
      this.setState({ timeZoneErr: "Please select time zone" });
    }

    if (
      this.state.totalCoding == 0 &&
      this.state.totalverbal == 0 &&
      this.state.totalMcq == 0
    ) {
      validate = false;
      alert("Assessment with no question choosen");
    }
    return validate;
  };
  sendSchedule = (total) => {
    if (this.validateFields()) {
      this.setState({ sendSchedule: true, scheduleCount: total });
      const payload = {
        jdId: this.state.optid,
        page: 1,
        limit: total,
        status: this.state.filterCan === "ALL" ? "" : this.state.filterCan,
      };
      this.getdata(payload);
    }
  };
  handleImportCancel = () => {
    this.setState({
      isShowImportModal: false,
      isShowQuesModal: false,
    });
  };

  handleSelectCandMore = () => {
    this.setState({ isShowImportModal: true, filterCan: "ALL", page: 1 });
    const payload = {
      jdId: this.state.optid,
      page: 1,
      limit: this.state.limit,
    };
    this.getdata(payload);
  };
  handleSelectQues = () => {
    this.setState({
      isShowQuesModal: true,
    });
  };
  handleTimer = () => {};

  filterCandData = (e) => {
    this.setState({
      filterCan: e.value,
      page: 1,
    });
    const payload = {
      jdId: this.state.optid,
      page: 1,
      limit: this.state.limit,
      status: e.value === "ALL" ? "" : e.value,
    };
    this.props.resumesourceAction.getCandidateById(payload);
  };
  handleBackPagination = (e) => {
    if (this.state.page !== 1) {
      const payload = {
        jdId: this.state.optid,
        page: this.state.page - 1,
        limit: 10,
        status: this.state.filterCan === "ALL" ? "" : this.state.filterCan,
      };
      this.props.resumesourceAction.getCandidateById(payload);
      this.setState({ page: this.state.page - 1 });
    }
  };
  handleNextPagination = () => {
    if (this.state.page !== this.state.totalPage) {
      const payload = {
        jdId: this.state.optid,
        page: this.state.page + 1,
        limit: 10,
        status: this.state.filterCan === "ALL" ? "" : this.state.filterCan,
      };
      this.props.resumesourceAction.getCandidateById(payload);
      this.setState({ page: this.state.page + 1 });
    }
  };

  handlePagination = async (e) => {
    await this.setState({
      page: e,
      // parseInt(e.target.innerText)
    });
  };

  handleBackPagination2 = async (e) => {
    if (this.state.page2 != 1) {
      await this.setState({ page2: this.state.page2 - 1 });
    }
  };
  handleNextPagination2 = async () => {
    if (this.state.page2 != this.state.totalPage2) {
      await this.setState({ page2: this.state.page2 + 1 });
    }
  };

  handlePagination2 = async (e) => {
    await this.setState({
      page2: e,
      // parseInt(e.target.innerText)
    });
  };

  handleCloseModal = () => {
    this.setState({
      showModal: false,
    });
  };

  handleCloseModalNew = () => {
    this.setState({
      showModalNew: false,
    });
  };

  handleDeleteQuestion = () => {
    let quesData = this.state.quesData.filter((item) => {
      return JSON.stringify(item) != JSON.stringify(this.state.currentQues);
    });

    this.setState({
      quesData: quesData,
      showModal: false,
    });
  };

  handleDeleteCandidate = () => {
    let candidateData = this.state.candidateData.filter((item) => {
      return item._id != this.state.currentCandidate.id;
    });

    this.setState({
      candidateData: candidateData,
      showModalNew: false,
    });
  };

  setLimit = (e) => {
    this.setState({
      limit: e,
      page: 1,
    });
  };

  setLimit2 = (e) => {
    this.setState({
      limit2: e,
      page2: 1,
    });
  };

  GetFormattedDate = (date) => {
    return (
      (date.getMonth() > 8
        ? date.getMonth() + 1
        : "0" + (date.getMonth() + 1)) +
      "/" +
      (date.getDate() > 9 ? date.getDate() : "0" + date.getDate()) +
      "/" +
      date.getFullYear()
    );
  };

  render() {
    const customStyles = {
      control: (base) => ({
        ...base,
        height: 34,
        minHeight: 34,
        maxHeight: 34,
        padding: 0,
        borderRadius: 4,
      }),
    };

    let optionsdata = this.state.optionsdata;
    let canFilterOptions = [
      { value: "ALL", label: "ALL" },
      { value: "NOT_SCHEDULED", label: "NOT SCHEDULED" },
      { value: "SCHEDULED", label: "SCHEDULED" },
    ];
    let quesData = this.state.quesData;

    if (this.state.page2 == 1) {
      quesData = quesData.slice(0, 10);
    } else {
      let data = quesData;
      let start = 10 * (this.state.page2 - 1);
      data = data.slice(start, start + 10);
      quesData = data;
    }

    let inboundDatacolumn = [
      {
        Header: () => <div style={{ textAlign: "left" }}>Name</div>,
        minWidth: 150,
        accessor: "name",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          // <div
          //   style={{
          //     textAlign: "left",
          //     paddingLeft: "10px",
          //   }}
          // >
          //   {row.value}
          // </div>
          <div style={{ display: "flex" }}>
            <div
              style={{
                // padding: "0 10px 0 20px"
                padding: "0 9px",
              }}
            >
              {(this.state.page - 1) * this.state.limit + row.index + 1}.
            </div>
            <div
              style={{
                textAlign: "center",
              }}
            >
              {row.value}
            </div>
          </div>
        ),
        // Header: "Name",
        // accessor: "name",
        // minWidth: 150,
        // style: { textAlign: "left" },
      },
      {
        Header: () => <div style={{ textAlign: "left" }}>Email</div>,
        minWidth: 200,
        accessor: "email",
        // Header: "Email",
        // accessor: "email",
        // minWidth: 200,
        style: { textAlign: "left" },
      },
    ];
    let inboundDatacolumn2 = [
      {
        // Header: "Questions",
        // accessor: "QuestionText",
        // minWidth: 150,
        // style: { textAlign: "left" },
        Header: () => <div style={{ textAlign: "left" }}>Questions</div>,
        minWidth: 150,
        accessor: "QuestionText",
        style: { whiteSpace: "initial" },
        Cell: (row) => (
          // <div
          //   style={{
          //     textAlign: "left",
          //     // paddingLeft: "10px",
          //   }}
          // >
          //   {row.value}
          // </div>
          <div style={{ display: "flex" }}>
            <div
              style={{
                // padding: "0 10px 0 20px"
                padding: "0 9px",
              }}
            >
              {(this.state.page2 - 1) * this.state.limit2 + row.index + 1}.
            </div>
            <div
              style={{
                textAlign: "left",
                whiteSpace: "initial",
              }}
            >
              {row.value}
            </div>
          </div>
        ),
      },
      {
        // Header: "Questions Time",
        // accessor: "Questionstime",
        // minWidth: 50,
        // style: { textAlign: "left" },
        Header: () => <div style={{ textAlign: "left" }}>Questions Time</div>,
        minWidth: 50,
        accessor: "Questionstime",
        style: { whiteSpace: "initial" },
      },
      {
        // Header: "Actions",
        // accessor: "action",
        // minWidth: 50,
        // style: { textAlign: "left" },
        Header: () => <div style={{ textAlign: "left" }}>Actions</div>,
        minWidth: 50,
        accessor: "action",
        style: { whiteSpace: "initial" },
        Cell: (val) => {
          return (
            <div className='schedule-button-user'>
              <div className='create'>
                <DeleteIcon
                  style={{
                    color: "red",
                    height: "20px",
                    width: "20px",
                    cursor: "pointer",
                  }}
                  onClick={() => {
                    this.setState({
                      showModal: true,
                      currentQues: val && val.original,
                    });
                  }}
                />
              </div>
            </div>
          );
        },
      },
    ];

    const {
      totalScore,
      totalVerbalScore,
      totalMcqScore,
      totalVerbalDuration,
      totalMcqDuration,
      totalverbal,
      totalMcq,
      totalCoding,
      totalCodingScore,
      totalCodingDuration,
      testOptions,
    } = this.state;
    return (
      <div className='main-form'>
        <Dialog
          className='dialog-box'
          open={this.state.showModalNew}
          onClose={this.handleCloseModalNew}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <DialogTitle id='alert-dialog-title' className='alert-dialog-title'>
            {`Are You Sure you want to delete this candidate?`}
          </DialogTitle>

          <DialogActions>
            <div onClick={this.handleDeleteCandidate} className='Modal-div'>
              Yes
            </div>

            <div onClick={this.handleCloseModalNew} className='Modal-div'>
              Cancel
            </div>
          </DialogActions>
        </Dialog>

        <Dialog
          className='dialog-box'
          open={this.state.showModal}
          onClose={this.handleCloseModal}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <DialogTitle id='alert-dialog-title' className='alert-dialog-title'>
            {`Are You Sure you want to delete this question?`}
          </DialogTitle>

          <DialogActions>
            <div onClick={this.handleDeleteQuestion} className='Modal-div'>
              Yes
            </div>

            <div onClick={this.handleCloseModal} className='Modal-div'>
              Cancel
            </div>
          </DialogActions>
        </Dialog>
        <div style={{ flexDirection: "column", width: "60%" }}>
          <div
            style={{
              display: "flex",
              width: "587px",
              justifyContent: "space-between",
            }}
          >
            <div style={{ flexDirection: "column" }}>
              <div className='text-schedule'>
                <strong>Select Job Description</strong>
              </div>
              <div className='sch-dropNew dropselect'>
                <SelectInput
                  className='schedule-select'
                  onChange={this.handleSelect}
                  options={optionsdata}
                  value={{
                    value: this.state.id,
                    label: this.state.name,
                  }}
                  placeholder={this.state.name ? this.state.name : "Job Title"}
                />
              </div>
              {this.state.name === "" && (
                <p className='errorMsg'>{this.state.jobTitleErr}</p>
              )}
            </div>

            <div className='sch-dropNew dropselect'>
              <div className='text-schedule'>
                <strong>Assessment</strong>
              </div>
              <SelectInput
                className={"schedule-select"}
                onChange={(e) => {
                  this.setState({ testSelected: e });
                }}
                value={this.state.testSelected}
                options={testOptions}
                placeholder={
                  this.state.testSelected && this.state.testSelected.label
                    ? this.state.testSelected.label
                    : "Select Test"
                }
              />
              {(this.state.testSelected === null ||
                !this.state.testSelected.label) &&
                this.state.testErr && (
                  <p className='errorMsg'>{this.state.testErr}</p>
                )}
            </div>
          </div>
          <div style={{ paddingTop: 0 }}>
            <div style={{ paddingTop: "8px" }}>
              <div
                className='sch-DateStyle card-containers'
                // style={{ margin: "10px 0px 10px 0px" }}
              >
                <div style={{ width: "286px", marginTop: "5px" }}>
                  <DatePicker
                    label=' Start Date'
                    className='date-new'
                    format='dd - MMM - yyyy'
                    name='datepicker'
                    isNoPast={true}
                    value={this.state.startDate}
                    onChange={(date) => this.setState({ startDate: date })}
                  />
                </div>
                <div style={{ width: "286px", marginTop: "5px" }}>
                  <TimePicker
                    className='date-new-2'
                    id='Start-time-picker'
                    label='Start Time'
                    name='timefrom'
                    value={this.state.timefrom}
                    onChange={(date) => {
                      this.setState({ timefrom: date });
                    }}
                  />
                </div>
              </div>
              <div
                className='sch-DateStyle card-containers'
                style={{ margin: "20px 0px 20px 0px" }}
              >
                <div style={{ width: "286px", marginTop: "5px" }}>
                  <DatePicker
                    label=' End Date'
                    className='date-new'
                    format='dd - MMM - yyyy'
                    name='datepicker'
                    isNoPast={true}
                    value={this.state.endDate}
                    min={this.state.startDate}
                    onChange={(date) => this.setState({ endDate: date })}
                  />
                </div>
                <div style={{ width: "286px", marginTop: "5px" }}>
                  <TimePicker
                    id='End-time-picker'
                    className='date-new-2'
                    label='End Time'
                    name='timeTo'
                    value={this.state.timeTo}
                    onChange={(date) => this.setState({ timeTo: date })}
                  />
                </div>
              </div>
              <div
                style={{
                  display: "flex",
                  marginBottom: "20px",
                  width: "587px",
                  justifyContent: "space-between",
                }}
              >
                <div>
                  <strong>
                    <div className='text-schedule'>Time Zone</div>
                  </strong>
                  <div className='sch-dropNew'>
                    <TimezoneSelect
                      className='date-new '
                      value={this.state.timeZone}
                      onChange={(e) => {
                        this.setState({
                          timeZone: e,
                        });
                      }}
                      styles={customStyles}
                    />
                    {this.state.timeZone === "" && (
                      <p className='errorMsg'>{this.state.timeZoneErr}</p>
                    )}
                  </div>
                </div>
                <div className='sch-dropNew '>
                  <div className='text-schedule'>
                    <strong>Allowed Devices</strong>
                  </div>
                  <SelectInput
                    className={"schedule-select"}
                    onChange={(e) => {
                      this.setState({ allowedDevices: e.value });
                    }}
                    options={this.state.allowedDevicesOptions}
                    placeholder={
                      this.state.allowedDevices
                        ? this.state.allowedDevices
                        : "Select Device"
                    }
                  />
                </div>
              </div>
              <div>
                <p className='text-schedule'>
                  <strong>Message</strong>
                </p>
                <div>
                  <JoditEditor
                    ref={this.setStateeditor}
                    value={this.state.content}
                    config={config}
                    key={9}
                    onBlur={(newContent) => this.handleUpdate(newContent)}
                    onChange={(newContent) => this.handleUpdate(newContent)}
                  />
                  <div
                    style={{ display: "none" }}
                    dangerouslySetInnerHTML={{ __html: this.state.content }}
                  />
                </div>
                {/* <RichTextEditor
                  className="richText"
                  value={this.state.des}
                  onChange={this.onChange}
                  blockStyleFn={getTextAlignClassName}
                />*/}
                {this.state.content === "" && this.state.messageErr && (
                  <p className='errorMsg'>{this.state.messageErr}</p>
                )}
              </div>
            </div>

            <div className='send' style={{ marginTop: "16px" }}>
              <Button
                backgroundColor='#04724D'
                color='#ffffff'
                width='200px'
                height='49'
                fontWeight='bold'
                className='sendButton'
                disabled={totalCoding == 0 && totalMcq == 0 && totalverbal == 0}
                onClick={() =>
                  this.sendSchedule(
                    this.props.resumesourceReducer.getCandidateByIdData &&
                      this.props.resumesourceReducer.getCandidateByIdData
                        .totalResults
                  )
                }
              >
                Send
              </Button>
              {this.state.open && (
                <Successmodal
                  open={this.state.open}
                  onClose={this.handleCloseSuccessModal}
                  text='Schedule Added Succesfully'
                />
              )}
            </div>
          </div>
        </div>
        <div style={{ width: "35%" }}>
          <div>
            <div>
              <div className='card sch-cardNew'>
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <p
                    style={{
                      paddingRight: "8px",
                      display: "flex",
                      alignItems: "center",
                      fontSize: "18px",
                      fontWeight: "700",
                    }}
                  >
                    Invite Details
                  </p>
                  <div className='image-new-added'>
                    <img src={Candidates} alt='' />
                  </div>
                </div>
                <div className='flex-div-new-schedule'>
                  <div className='number-count'>
                    {this.props.resumesourceReducer.getCandidateByIdData &&
                    this.props.resumesourceReducer.getCandidateByIdData
                      .totalResults
                      ? this.props.resumesourceReducer.getCandidateByIdData
                          .totalResults
                      : 0}
                  </div>
                </div>
                <ul className='d-flex sch-list'>
                  <li
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      width: "100%",
                      cursor: "pointer",
                      alignItems: "center",
                    }}
                  >
                    <div className='numberOf'>Number of Candidates </div>
                    <div className='moredetails'>
                      <a
                        onClick={this.handleSelectCandMore}
                        className='moreButton btnNewLine'
                      >
                        More
                        <span className='chevron'>
                          <img src={Chevron} />
                        </span>
                      </a>
                    </div>
                  </li>
                </ul>
              </div>

              <div className='card sch-cardNew AssessementSummary'>
                <div className='title'>Assessment Summary</div>
                {totalverbal > 0 ? (
                  <div className='infoCardArea'>
                    <div className='titleArea'>
                      <img src={SpeakingIcon} alt='' />{" "}
                      <span>Verbal Interview</span>
                    </div>
                    <div className='contentArea'>
                      <span className='contentMain'>
                        No.Of Questions : {totalverbal}
                      </span>
                      <span className='content'>
                        Total Time : {secondsToMinutes(totalVerbalDuration)}
                      </span>
                      <span className='content'>
                        Score : {totalVerbalScore}/{totalScore}
                      </span>
                    </div>
                  </div>
                ) : null}
                {totalMcq > 0 ? (
                  <div className='infoCardArea'>
                    <div className='titleArea'>
                      <img src={McqIcon} alt='' /> <span>MCQ</span>
                    </div>
                    <div className='contentArea'>
                      <span className='contentMain'>
                        No.Of Questions : {totalMcq}
                      </span>
                      <span className='content'>
                        Total Time : {secondsToMinutes(totalMcqDuration)}
                      </span>
                      <span className='content'>
                        Score : {totalMcqScore}/{totalScore}
                      </span>
                    </div>
                  </div>
                ) : null}
                {totalCoding > 0 ? (
                  <div className='infoCardArea'>
                    <div className='titleArea'>
                      <img src={Coding} alt='' /> <span>Coding Interview</span>
                    </div>
                    <div className='contentArea'>
                      <span className='contentMain'>
                        No.Of Questions : {totalCoding}
                      </span>
                      <span className='content'>
                        Total Time : {secondsToMinutes(totalCodingDuration)}
                      </span>
                      <span className='content'>
                        Score : {totalCodingScore}/{totalScore}
                      </span>
                    </div>
                  </div>
                ) : null}
              </div>
            </div>
          </div>
        </div>
        <PrintModal
          style={{ zIndex: 0 }}
          className='sch-modal'
          isShowModal={this.state.isShowImportModal}
          onCloseModal={this.handleImportCancel}
        >
          <div>
            <div className='show-cand-card'>
              <p style={{ fontSize: "18px", fontWeight: "700" }}>
                Select Candidates
              </p>
              <div className='new-wrapper'>
                <div className='show-cand-status-schedule'>
                  <div className='card show-cand-cards-schedule'>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                      }}
                    >
                      <img src={CandidatesNew} alt='' />
                    </div>
                    <div className='show-text-schedule text1'>
                      <div className='new-number'>
                        {this.props.resumesourceReducer.getCandidateByIdData &&
                        this.props.resumesourceReducer.getCandidateByIdData
                          .totalResults
                          ? this.props.resumesourceReducer.getCandidateByIdData
                              .totalResults
                          : 0}
                      </div>
                      <div style={{ fontSize: "14px" }}>Total Candidates</div>
                    </div>
                  </div>
                </div>

                <div className='sch-dropNew dropselect'>
                  <SelectInput
                    className='react-select project'
                    onChange={this.filterCandData}
                    options={canFilterOptions}
                    value={{
                      value: this.state.filterCan,
                      label: this.state.filterCan,
                    }}
                  />
                </div>
              </div>
              <TableComponent
                className='schedule-csv-table'
                justifyContent='center'
                data={this.state.candidateData}
                // data={data2}
                columns={inboundDatacolumn}
                // defaultPageSize={5}
                noDataText={"No data found"}
                sortable={true}
                showBottomPagination={true}
                pageSize={this.state.limit}
                setPageSize={(e) => this.setLimit(e)}
                gotoPage={(e) => this.handlePagination(e)}
                totalPage={this.state.totalPage}
                pageNumber={this.state.page ? this.state.page : 1}
                handlePrev={this.handleBackPagination}
                handleNext={this.handleNextPagination}
              />
              {/* <Pagination
                totalPage={this.state.totalPage}
                handleBack={this.handleBackPagination}
                handleNext={this.handleNextPagination}
                activePage={this.state.page ? this.state.page : 1}
                handlePagination={this.handlePagination}
              /> */}
            </div>
          </div>
        </PrintModal>
        <PrintModal
          className='sch-modal'
          isShowModal={this.state.isShowQuesModal}
          onCloseModal={this.handleImportCancel}
        >
          <div>
            <div className='show-cand-card'>
              <h3>Select Questions</h3>

              <TableComponent
                justifyContent='center'
                data={quesData}
                columns={inboundDatacolumn2}
                showBottomPagination={true}
                pageSize={this.state.limit2}
                setPageSize={(e) => this.setLimit2(e)}
                gotoPage={(e) => this.handlePagination2(e)}
                totalPage={this.state.totalPage2}
                pageNumber={this.state.page2 ? this.state.page2 : 1}
                handlePrev={this.handleBackPagination2}
                handleNext={this.handleNextPagination2}
              />
              {/* <Pagination
                totalPage={this.state.totalPage2}
                handleBack={this.handleBackPagination2}
                handleNext={this.handleNextPagination2}
                activePage={this.state.page2 ? this.state.page2 : 1}
                handlePagination={this.handlePagination2}
              /> */}
            </div>
          </div>
        </PrintModal>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    ScheduleReducer: state.ScheduleReducer,
    JobDataReducer: state.JobDataReducer,
    JobDescriptionReducer: state.JobDescriptionReducer,
    resumesourceReducer: state.resumesourceReducer,
    testReducer: state.AssessmentReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    ScheduleAction: bindActionCreators(ScheduleActions, dispatch),
    JobDataAction: bindActionCreators(JobDataAction, dispatch),
    JobDescriptionAction: bindActionCreators(JobDescriptionAction, dispatch),
    resumesourceAction: bindActionCreators(CActions, dispatch),
    testAction: bindActionCreators(TestAction, dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Schedule));
